import * as React from 'react'
import './Footer.css'

const Footer = () => {
  const getDate = () => {
    const date = new Date()
    return date.getFullYear()
  }
  return (
    <footer id='footer'>
      <div className='row footer-main'>
        <div className='col-twelve tab-full left footer-desc'>
          <div className='footer-logo' data-sal='flip-down' data-sal-delay='100' data-sal-easing='ease-in-out' />
          Due to the fear of getting spam attacked if i put my email here, i have set links to the right side of the screen
          for you to contact me. Reach out to me!
        </div>
      </div>
      <div className='row footer-bottom'>
        <div className='col-twelve'>
          <div className='copyright'>
            <span className='copright'>Nicolas Caballero © {getDate()} - All rights reserved </span>
          </div>
          <div className='go-top' style={{ display: 'block' }}>
            <a title='Back to Top' href='#home' className='go-up-btn'>
              <i className='icon-arrow-up' aria-hidden='true' />
            </a>
          </div>
        </div>
      </div>
    </footer>
  )
}

export default Footer
