import * as React from 'react'
import './Contact.css'
import AccountIcon from '../AccountIcon/AccountIcon'

const Contact = ({ accounts }) => {
  return (
    <section id='contact' className='s-works s-contact'>
      <div className='intro-wrap'>
        <div className='row section-header'>
          <div className='col-full text-center'>
            <h3 className='subhead'>Contact me</h3>
          </div>
        </div>
        <div className='row'>
          <h2 className='col-twelve text-center text-white add-top'>You can find me on these sites</h2>
        </div>
        <div className='row add-top text-center' data-sal='zoom-in' data-sal-delay='150' data-sal-easing='ease-in-out'>
          {accounts.map((account) => (
            <div className='col-four' key={account.id}>
              <AccountIcon {...account} className='text-white' />
            </div>
          ))}
        </div>
      </div>
      <div className='color-padding'></div>
    </section>
  )
}

export default Contact
