import * as React from 'react'
import './About.css'

const About = () => {
  return (
    <section id='about' className='s-about' data-sal='slide-left' data-sal-delay='100' data-sal-easing='ease-in-out'>
      <div className='row section-header add-bottom'>
        <div className='col-full'>
          <h1 className='display-1 display-1--light text-center'>About Me</h1>
        </div>
      </div>
      <div className='row about-desc'>
        <div className='col-full'>
          <p>
            I'm a self taught developer, i have 3 years of experience in the field, soon in life i found out that i loved
            computers and everything related to them, it might look like I started programming not so long ago, but altough
            that's true, actually i've been working with computers since i was a child.
          </p>
        </div>
      </div>
    </section>
  )
}

export default About
