import * as React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { Helmet } from 'react-helmet'
import Header from '../components/Header/Header'
import Home from '../components/Home/Home'
import About from '../components/About/About'
import Skills from '../components/Skills/Skills'
import Works from '../components/Works/Works'
import Contact from '../components/Contact/Contact'
import Footer from '../components/Footer/Footer'
import '../css/base.css'
import '../css/globals.css'
import '../css/responsive.css'

const accounts = [
  { id: 1, name: 'Github', url: 'https://github.com/N-bred', iconName: 'fa-github' },
  { id: 2, name: 'Fiverr', url: 'https://es.fiverr.com/nbredr?public_mode=true', iconName: 'fa-first-order' },
  { id: 3, name: 'Upwork', url: 'https://www.upwork.com/freelancers/~01d718fe9d2c966ae8', iconName: 'fa-briefcase' },
]

const IndexPage = () => {
  React.useEffect(() => {
    document.body.style.backgroundColor = '#000'
  }, [])

  const {
    allProject: { nodes },
  } = useStaticQuery(graphql`
    query MyQuery {
      allProject {
        nodes {
          id
          name
          img
          description
          url_github
          url_preview
          date
        }
      }
    }
  `)

  return (
    <div>
      <Helmet>
        <meta charSet='utf-8' />
        <meta name='author' content='Nicolas Caballero'></meta>
        <meta name='description' content='Portfolio site for software development'></meta>
        <title>Nicolas Caballero - Portfolio</title>
        <link rel='canonical' href='https://n-bred.github.io/' />
      </Helmet>
      <Header />
      <Home accounts={accounts} />
      <About />
      <Skills />
      <Works gridNumber={2} gridSize='block-1-2' projectsArray={nodes} minHeight='600px' cardWidth='45%' />
      <Contact accounts={accounts} />
      <Footer />
    </div>
  )
}

export default IndexPage
