import * as React from 'react'
import './Home.css'

import AccountIcon from '../AccountIcon/AccountIcon'

const Home = ({ accounts }) => {
  return (
    <section id='home' className='s-home target-section'>
      <div className='overlay' />
      <div className='shadow-overlay' />
      <div className='home-content'>
        <div className='row home-content__main'>
          <h3 className='text-colored' data-sal='fade' data-sal-delay='600' data-sal-easing='ease-in-out'>
            Welcome to Nicolas Caballero's Web Portfolio
          </h3>
          <h1 data-sal='slide-down' data-sal-delay='900' data-sal-duration='900' data-sal-easing='ease-in-out'>
            I'm a <span className='text-colored'>software developer</span> <br />
            who enjoys making nice and functional apps and websites
            <br />
          </h1>
          <div
            className='home-content__buttons'
            data-sal='fade'
            data-sal-delay='1000'
            data-sal-duration='1000'
            data-sal-easing='ease-in-out'
          >
            <a href='#contact' className='btn btn--stroke'>
              Work with me
            </a>
            <a href='#about' className='btn btn--stroke'>
              About me
            </a>
          </div>
        </div>
        <div className='home-content__scroll align-center full-width'>
          <a href='#about' className='scroll-link'>
            <span>Scroll Down</span>
          </a>
        </div>

        <div className='home-content__line' />
      </div>
      <ul className='home-social'>
        {accounts.map((account) => (
          <li key={account.id}>
            <AccountIcon {...account} />
          </li>
        ))}
      </ul>
    </section>
  )
}

export default Home
