import * as React from 'react'
import './Skills.css'

const Skills = () => {
  return (
    <section id='services' className='s-services'>
      <div className='row section-header add-bottom'>
        <div className='col-full'>
          <h1 className='display-2 text-center' data-sal='zoom-in' data-sal-delay='100' data-sal-easing='ease-in-out'>
            My Skills
          </h1>
        </div>
      </div>
      <div className='row services-list block-1-2 block-tab-full'>
        <div className='col-block service-item' data-sal='slide-up' data-sal-delay='100' data-sal-easing='ease-in-out'>
          <div className='service-icon'>
            <i className='icon-cube' />
          </div>
          <div className='service-text'>
            <h3 className='h2'>Scripting</h3>
            <p>
              With languages such as Python and Javascript i can automate processes and boring tasks that are often
              repetitive and time consuming, i have worked on several scripts to get things done in different operative
              systems such as windows and many distributions of linux.
            </p>
          </div>
        </div>
        <div className='col-block service-item' data-sal='slide-up' data-sal-delay='100' data-sal-easing='ease-in-out'>
          <div className='service-icon'>
            <i className='icon-lego-block' />
          </div>
          <div className='service-text'>
            <h3 className='h2'>Web Development</h3>
            <p>
              I've been making sites since i started programming, i make use of many technologies such as <br />
              - HTML5, CSS3, Javascript, Python <br />
              - CSS Frameworks and Dynamic CSS tools <br />
              - Javascript Frameworks: React JS - Svelte
              <br />
            </p>
          </div>
        </div>
        <div className='col-block service-item' data-sal='slide-up' data-sal-delay='100' data-sal-easing='ease-in-out'>
          <div className='service-icon'>
            <i className='icon-earth' />
          </div>
          <div className='service-text'>
            <h3 className='h2'>Game Developer</h3>
            <p>
              As a side gig, i enjoy making simple games mostly in Python and Unity, I worked with a team of friends in the
              2021 Gamejam and as a result, we made the most loved game in our country
            </p>
          </div>
        </div>
        <div className='col-block service-item' data-sal='slide-up' data-sal-delay='100' data-sal-easing='ease-in-out'>
          <div className='service-icon'>
            <i className='icon-group' />
          </div>
          <div className='service-text'>
            <h3 className='h2'>Desktop App Development</h3>
            <p>I can make desktop apps with Python: Tkinter - PyQt5, Javascript: Electron and C#: Windows Forms.</p>
          </div>
        </div>
        <div className='col-block service-item' data-sal='slide-up' data-sal-delay='100' data-sal-easing='ease-in-out'>
          <div className='service-icon'>
            <i className='icon-headset' />
          </div>
          <div className='service-text'>
            <h3 className='h2'>Bots Programming</h3>
            <p>
              I have experience programming bots for different purposes such as Web alerts, Cryptocurrency mining, Automating
              SMS, and many more.
            </p>
          </div>
        </div>
        <div className='col-block service-item' data-sal='slide-up' data-sal-delay='100' data-sal-easing='ease-in-out'>
          <div className='service-icon'>
            <i className='icon-info' />
          </div>
          <div className='service-text'>
            <h3 className='h2'>Web Scrapping</h3>
            <p>Along with bots i can also write apps to retrieve information via web scrapping.</p>
          </div>
        </div>
      </div>
    </section>
  )
}

export default Skills
